import {
  FormControl,
  Form,
  Button,
  Navbar,
  Container,
  Nav
} from "react-bootstrap";
import React from "react";
import LinkedCameraRoundedIcon from "@material-ui/icons/LinkedCameraRounded";
function Navigation() {
  return (
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" fixed="top">
      <Container>
        <Navbar.Brand href="#home">
          <LinkedCameraRoundedIcon
            className="mb-2 me-2"
            style={{ fontSize: 40 }}
          />{" "}
          PikShare
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ms-auto ">
            <Form className="d-flex">
              <FormControl
                type="search"
                placeholder="Search"
                className="me-4 mt-2 mb-2"
                aria-label="Search"
                id="Search_bar"
              />
              {/* <Button variant="outline-success">Search</Button> */}
            </Form>
            <Button variant="outline-primary" className="me-4 mt-2 mb-2">
              SignUp
            </Button>
            <Button variant="outline-light" className="me-4 mt-2 mb-2">
              LogIn
            </Button>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}
export default Navigation;
